import React, { useState, useEffect } from "react";

import Filter from "./components/Filter";
import PersonForm from "./components/PersonForm";
import Persons from "./components/Persons";
import Notification from "./components/Notification";

import personServices from "./services/persons";

const Header = ({ title }) => <h2>{title}</h2>;

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [fPersons, setFPersons] = useState([]);
  const [filter, setFilter] = useState("");
  const [message, setMessage] = useState({});

  useEffect(() => {
    personServices.getAll().then(persons => {
      setFPersons(persons);
      setPersons(persons);
    });
  }, []);

  const handleName = e => {
    setNewName(e.target.value);
  };
  const handleNumber = e => {
    setNewNumber(e.target.value);
  };

  const handleSubmit = e => {
    e.preventDefault();
    const person = {
      name: newName,
      number: newNumber
    };
    if (!nameExists(person.name)) {
      personServices
        .createPerson(person)
        .then(returnPerson => {
          setMessage({ text: `Added ${person.name}`, type: "success" });
          setTimeout(() => {
            setMessage({});
          }, 5000);
          setFPersons(fPersons.concat(returnPerson));
          setPersons(persons.concat(returnPerson));
        })
        .catch(error => {
          console.log("...", error.response.data);
          setMessage({
            text: error.response.data.error,
            type: "error"
          });
        });
    } else {
      const result = window.confirm(
        `${person.name} is already added to phonebook, replace the old number with a new one?`
      );
      if (result) {
        const id = nameExists(person.name).id;
        personServices
          .updatePerson(id, person)
          .then(returnedPerson => {
            const newPersons = persons.map(p =>
              p.id !== id ? p : returnedPerson
            );
            setFPersons(newPersons);
            setPersons(newPersons);
          })
          .catch(error => {
            setMessage({
              text: `Information of ${person.name} has already been removed from server`,
              type: "error"
            });
            const newPersons = persons.filter(p => id !== p.id);
            setFPersons(newPersons);
            setPersons(newPersons);
          });
      }
    }
    setNewName("");
    setNewNumber("");
  };

  const nameExists = name => persons.find(person => person.name === name);

  const handleFilter = e => {
    setFilter(e.target.value);
    const filterPersons = persons.filter(person =>
      person.name.toLowerCase().includes(e.target.value.toLowerCase())
    );
    setFPersons(filterPersons);
  };

  const handleDelete = person => {
    const result = window.confirm(`Delete ${person.name}`);
    if (result) {
      personServices.deletePerson(person.id).then(status => {
        if (status === 204) {
          setPersons(persons.filter(p => p.id !== person.id));
          setFPersons(persons.filter(p => p.id !== person.id));
        }
      });
    }
  };

  return (
    <div>
      <Header title="Phonebook" />
      <Notification type={message.type}>{message.text}</Notification>
      <Filter filter={filter} onChange={handleFilter} />
      <Header title="add a new" />

      <PersonForm
        name={newName}
        number={newNumber}
        onSubmit={handleSubmit}
        onChangeName={handleName}
        onChangeNumber={handleNumber}
      />

      <Header title="Numbers" />

      <Persons persons={fPersons} deletePerson={handleDelete} />
    </div>
  );
};

export default App;
